@font-face {
  font-family: "PublicSans-Bold";
  src: url("/src/fonts/PublicSans-Bold.ttf") format("truetype");
}

@font-face {
  font-family: "PublicSans-Light";
  src: url("/src/fonts/PublicSans-Light.ttf") format("truetype");
}

a {
  text-decoration: none;
  color: seagreen;
}

a:hover {
  color: black;
}

.aboutvideo {
  border: 2px solid lightgray;
  border-radius: 25px;
  padding: 10px;
  margin-bottom: 50px;
}

.app {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}


body,
html {
  height: 100%;
  width: 100%;
  overflow-x: hidden;
  margin: 0;
  padding: 0;
  color: #228c22;
  background-color: #EBF1E9;
  font-family: "PublicSans-Light", sans-serif;
}

.bg {
  width: 100%;
}

.footer {
  width: 100%;
  size: 1.5em;
  height: 1.5em;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-decoration: none;
  background-color: #004225;
  color: white;
  margin-top: 20px;
  margin-bottom: 0;
  padding-top: 20px;
  padding-bottom: 20px;
  padding-left: 30px;
  font-family: "PublicSans-Bold", sans-serif;
  font-size: 12px;
}

html,
body {
  height: 100%;
}

h1 {
  font-family: "PublicSans-Bold", sans-serif;
  margin-top: 8px;
  margin-bottom: 8px;
}

.large-font {
  font-size: 18px;
}

.logo {
  margin: 5px;
  border-radius: 25px;
  width: 200px;
}

.logocontainer {
  background-color: white;
  border-radius: 25px;
  width: fit-content;
  margin-top: -10px;
  display: block;
}

.main {
  width: 70%;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 60px;
}

.nav {
  width: 100%;
  size: 1.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-decoration: none;
  background-color: #6AB650;
  color: white;
  padding-top: 15px;
  padding-bottom: 21px;
  font-family: "PublicSans-Bold", sans-serif;
  font-size: 20px;
}

.navlink {
  position: relative;
  margin-top: 0;
  width: 100%;
  display: flex;
  justify-content: space-around;
  gap: 50px;
}

.navlink>a {
  color: white !important;
  text-decoration: none;
}

.navlink>a:hover {
  color: darkgreen !important;
}

.overlap {
  position: relative;
  width: 100%;
  z-index: 1;
}

.aboutp {
  inline-size: 70ch;
  margin-top: 8px;
}


.page {
  position: relative;
  margin-top: -220px;
  z-index: 2;
  padding-top: 20px;
  padding-bottom: 10px;
  background: rgba(255, 255, 255, .9);
  /* background-color: white; */
  border-radius: 25px;
  border: 1px solid lightgray;
  width: 70%;
  margin-right: auto;
  margin-left: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.smallimage {
  width: 320px;
  height: 240px;
  padding-bottom: 40px;
}

.spacedul>li {
  margin-bottom: 10px;
}

.subpage {
  background-color: #d2e8ba;
  font-family: "PublicSans-Light", sans-serif;
  color: #228c22;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 5px;
  padding-left: 50px;
  padding-right: 150px;
  border: 5px #228c22;
  border-left-style: solid;
  border-right-style: solid;
  border-radius: 5px;
}

.textcontainer {
  inline-size: 70ch;
  width: 70%;
  margin-left: auto;
  margin-right: auto;
}

ul {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

/* temporary solution for responsiveness */
@media (max-width:1000px) {
  .page {
    width: 90%;
    margin-top: -4vh;
    padding: 15px;
  }

  .aboutvideo {
    flex: 1;
    width: 95%;
    padding: 0;
  }

  .subpage {
    width: 95%;
    padding: 0;
    padding-left: 0;
    padding-right: 0;
    margin: 0;
  }

  .aboutp {
    width: 100%;
  }

  .navlink {
    gap: 10px;
  }

  .spacedul>li {
    width: 70%;
  }

  .spacedul>li>a {
    width: 70%;
  }

  .textcontainer {
    width: 100%;
  }

  a {
    width: 60%;
  }

  li {
    width: 70%;
  }

}